import React from 'react';
import './App.css';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'; 
import MainPage from './pages/main.js';
import CodePage from './pages/programming.js';
import VidPage from './pages/video.js';
import ArtPage from './pages/art.js';
import { CardGroup, Card } from 'react-bootstrap';
import Navigation, { mainPage } from './components/navigation';

function App() {
  return (
    <div className="App">
            <Navigation />
            <Switch>
                <Route exact path="/">
                  <MainPage />
                </Route>
                <Route path="/pages/programming">
                  <CodePage />
                </Route>
                <Route path="/pages/video">
                  <VidPage />
                </Route>
                <Route path="/pages/art">
                  <ArtPage />
                </Route>          
            </Switch>
    </div>
  );
}

export default App;
