import React, {Component} from 'react';
import {Navbar}  from 'react-bootstrap';
import './navigation.css';
import { NavLink } from 'react-router-dom'


var main=true;
var art=false;
var vid=false;
var code=false;
class navigation extends Component{
    render(){
        return(
        <div>
            <Navbar className="NavBar">
                <Navbar.Brand className="title">Argha's Space</Navbar.Brand>
                <div className="Navlinks">
                <NavLink className={main ? "NavItem active": "NavItem"} to="/" onClick={mainPage}>Home</NavLink>
                <NavLink className={art ? "NavItem active": "NavItem"} to="/pages/art" onClick={artPage}>Art</NavLink>
                <NavLink className={vid ? "NavItem active": "NavItem"} to="/pages/video" onClick={vidPage}>Video</NavLink>
                <NavLink className={code ? "NavItem active": "NavItem"} to="/pages/programming" onClick={codePage}>Code</NavLink>
                </div>
            </Navbar>
        </div>
        )
    }
}
export function mainPage(){
    main=!main
    console.log("main=" + main);
}
export function artPage(){
    art=!art
    main=!main
    console.log("art="+art);
}
export function vidPage(){
    main=false;
    art=false;
    vid=true;
    code=false;
    console.log("vid="+vid);
}
export function codePage(){
    main=false;
    art=false;
    vid=false;
    code=true;
    console.log("code="+code);
}

export default navigation;

