import '../App.js';
import Navigation, { mainPage } from '../components/navigation';
import React, {Component} from 'react';
import './pages.css';
import { Card, CardGroup } from 'react-bootstrap';

function main(){
    mainPage();
    return(
        <div>
            <CardGroup className="cardBackground">
                <div className='firstelement'>
                    <Card className="cardOverall">
                        <Card.ImgOverlay>
                            <Card.Title>Element 1</Card.Title>
                        </Card.ImgOverlay>
                    </Card>
                </div>
            </CardGroup>
        </div>
    );
}
export default main;